                  '④. 优惠券应在载明的有效期内使用，过期作废，请珍惜每张优惠券哦；',
                  '⑤. 每张优惠券的具体使用条件请查看对应优惠券的使用说明；',
                  '⑥. 按满足优惠券使用条件的商品统计，优惠券会按比例抵扣在每个符合条件的商品上',
                  '⑦. 使用优惠券支付的订单，若产生退货，优惠券均不退回，退款金额按优惠后的小计金额退款。'
                ]
              },
              {
                h3: '4.获取途径',
                p: ['①. 站内：通过活动赠送、客服补偿等获得（系统自动添加，无需兑换）', '②. 站外：通过外部合作渠道获得的优惠券兑换码，在“我-我的优惠券”找到兑换码输入框，输入获得的兑换码即可兑换优惠券（请在有效期内兑换）']
              }
            ]
