            h2: '售后说明',
            list: [
              {
                h3: '1.关于签收',
                p: [
                  '为了保障您的权益，在签收之前，请和邮局或快递公司递送人员一起拆开包裹，当面清点货品，一旦签收视为对订单商品的认可（产品质量问题除外）。',
                  '主要检查如下内容：',
                  '①.封箱胶带是否完好无损，胶带是否有被拆开重新粘贴过的痕迹；',
                  '②.外包装是否破损；',
                  '③.商品是否缺少、配送错误。',
                  '如在检查的过程中出现问题，需快递员签字证明问题才可签收，并及时联系唯妮海购客服，我们会第一时间处理您的问题；如没有快递员签字证明破损或少货，唯妮海购有权拒绝赔付。'
                ]
              },
              {
                h3: '2.关于拒收',
                p: [
                  '客户拒签的：',
                  '①.非因唯妮海购平台原因造成的拒签的（包括但不限于非质量问题的拒签），由合作方自行与客户沟通解决，唯妮海购不承担相应责任。',
                  '②.其他如质量或者物流产生的问题，唯妮海购会根据实际情况作出相应补偿。',
                  '③.由于国际直邮时间会比较长，如遇海关清关量比较大、商品滞留海关等情况，唯妮海购暂不提供退换货等服务。'
                ]
              },
              {
                h3: '3.关于退换货',
                p: [
                  '由于唯妮海购商品均为直邮、保税区发货，商品无法直接退回发货地 ，故不支持无理由退换货，如有需要可以重新购买！ 以下两种情况烦请跟客 服沟通，提供相应凭证后会尽快处理。',
                  '①.商品存在质量问题，如严重破损影响使用等（货物途中包裹受不同程度挤压、抛甩，使部分商品发生变形碎裂，不影响使用属于正常现象， 不属于质量问题）。',
                  '②.商家的原因，错发或者漏发商品。（仅限外包装完整无破损，封胶正常无重贴的情况下，以及拆封后发现产品与订单不符。）'
                ]
